
import XHeader from 'vux/src/components/x-header/index.vue'

import Progress from "@/components/progress";
import scroll from "@/components/scrollAll";
export default {
data() {
return {
List: [],
pageIndex: 1,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
Total: 0
};
},
components: {
XHeader,
Progress,
scroll
},
filters: {
time(day) {
return dayjs(day).format("YYYY-MM-DD");
},
schedule(count, total) {
if (!count) {
return 0;
} else {
return Number.parseFloat(((count / total) * 100).toFixed(2));
}
}
},
computed: {
queryName() {
return this.$route.query.name === "订单";
}
},
methods: {
getOrder(pageIndex) {
this.$vux.loading.show({
text: "加载中"
});
const url = ["/Order/GetMyOrderList", "/Cost/GetMyReturnedMoney"];
let params = Object.assign({}, this.$route.query, {
page: this.pageIndex
});
params.size = null;
params.name = null;
axios.get(url[this.queryName ? 0 : 1], { params }).then(res => {
const data = res.data.Data;
if (pageIndex) {
this.List = [...this.List, ...data.List];
} else {
this.List = data.List;
}
this.Total = data.TotalCount;
this.$refs.scroll.forceUpdate();
setTimeout(() => {
this.$vux.loading.hide();
}, 500);
});
},
listPullup() {
this.pageIndex++;
this.getOrder(2);
this.$refs.scroll.scroll.finishPullUp();
this.$refs.scroll.refresh();
},
listPulldown() {
this.pageIndex = 1;
this.getOrder();
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.refresh();
},
init() {
let hei = document.body.clientHeight;
this.$refs.clientList.style.height = `${hei - 5}px`;
}
},
mounted() {
this.init();
this.getOrder();
}
};



